        "buffalo",
        "crocodile",
        "kangaroo",
        "ostrich",
        "rabbit",
        "bacon",
        "ham",
        "coconut",
        "nut",
        "fish",
        "gemfish",
        "kingfish",
        "milkfish",
        "salmon",
        "shark",
        "tuna",
        "trout",
        "potato",
        "corn",
        "corn chips",
        "soup",
        "chocolate",
        "jam",
        "honey",
        "broccoli",
        "cabbage",
        "capsicum",
        "carrot",
        "cassava",
        "cauliflower",
        "chives",
        "fennel",
        "gherkin",
        "leek",
        "lettuce",
        "melon",
        "mushroom",
        "olive",
        "parsley",
        "pumpkin",
        "radish",
        "spinach",
        "tomato",
        "watercress",
        "cucumber",
        "avocado",
        "lemon",
        "melon",
      ];
      const pinyin = [
        "curry",
        "ginger",
        "apple",
        "wine",
        "cocoa",
        "beverage",
        "coffee",
        "cordial",
        "fruit drink",
        "juice",
        "soft drink",
        "tea",
        "water",
        "biscuit",
        "cone",
        "breadcrumbs",
        "bread",
        "muffin",
        "breakfast cereal",
        "cake",
        "doughnut",
        "scone",
        "porridge",
        "amaranth",
        "barley",
        "millet",
        "oats",
        "wheat",
        "rice",
        "flour",
        "pizza",
        "noodle",
        "pasta",
        "lasagne",
        "macaroni",
        "spaghetti",
        "croissant",
        "pastry",
        "sausage",
        "pie",
        "spring roll",
        "butter",
        "cheese",
        "milk",
        "yoghurt",
        "egg",
        "oil",
        "apricot",
        "banana",
        "blackberry",
        "cherry",
        "cumquat",
        "cranberry",
        "feijoa",
        "goji",
        "grape",
        "grapefruit",
        "guava",
        "jackfruit",
        "lemon peel",
        "loquat",
        "mulberry",
        "orange",
