        "beef",
        "veal",
        "lamb",
        "pork",
        "chicken",
        "turkey",
        "duck",
        "quail",
        "buffalo",
        "crocodile",
        "kangaroo",
        "ostrich",
        "rabbit",
        "bacon",
        "ham",
        "coconut",
        "nut",
        "fish",
        "gemfish",
        "kingfish",
        "milkfish",
        "salmon",
        "shark",
        "tuna",
        "trout",
        "potato",
        "corn",
        "corn chips",
        "soup",
        "chocolate",
        "jam",
        "honey",
        "broccoli",
        "cabbage",
        "capsicum",
        "carrot",
        "cassava",
        "cauliflower",
        "chives",
        "fennel",
        "gherkin",
        "leek",
        "lettuce",
        "melon",
        "mushroom",
        "olive",
        "parsley",
        "pumpkin",
        "radish",
        "spinach",
        "tomato",
        "watercress",
        "cucumber",
        "avocado",
        "lemon",
        "melon",
      ];
      cs.forEach((item, index) => {
        data.push({
          label: item,
          key: index,
          pinyin: pinyin[index],
        });
      });
      return data;
    };
    return {
      food: "",
      data: generateData(),
      value: [],
      filterMethod(query, item) {
        return item.pinyin.indexOf(query) > -1;
      },
      gender: "Female",
      numberValidateForm: {
        age: "",
      },
      result: [],
      target: {},
      showtarget: false,
    };
  },
  methods: {
    // send ajax request to get meal
    async send() {
      let result = await getmeal(this.food);
      this.result = result.data || result;
      // flattening array
      this.result = this.result.flat();
      console.log(result.data);
      // get Recommended Daily Nutrient Allowances
      let t = await getTarget(this.numberValidateForm.age, this.gender);
      this.target = t.data[0];
      // console.log(this.target);
      this.showtarget = true;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.numberValidateForm.age < 60) {
            this.$message.error("Age must be over 59");
            return;
          } else if (this.numberValidateForm.age > 110) {
            this.$message.error("Age must be under 110");
            return;
          }
          // get the food name based on the index
          let len = this.value.length;
          if (len == 0) {
            this.$message.error("Must select some foods");
            return;
          }
          for (let i = 0; i < len; i++) {
            this.food += this.data[this.value[i]].label + ",";
          }
          this.food = this.food.substring(0, this.food.length - 1);
          // console.log(this.food)
          this.send();
        } else {
          this.$message.error("error submit");
          return false;
        }
      });
    },
    listchange(v, dir, mk) {
      if (dir == "left") {
        this.open2();
      } else {
        this.open1();
      }
    },
    // add success notify
    open1() {
      this.$notify({
        title: "Added",
        message: `successfully added`,
        type: "success",
      });
    },
    // remove notify
    open2() {
      this.$notify({
        title: "Removed",
        message: `successfully removed`,
        type: "warning",
      });
    },
  },
};
</script>
<style scoped>
