  top: 30px;
  transform: translate(-50%);
  display: flex;
  align-items: center;
  font-size: 30px;
  text-align: center;
  margin-top: 20px;
  font-family: "Comic Sans MS", "Courier New", Courier, monospace;
}
.title .line {
  width: 95px;
  height: 2px;
  background-color: #cb980f;
  margin: auto auto;
}
