  min-width: 900px;
  border-collapse: collapse;
}

.customers td,
.customers th {
  font-size: 1em;
  border: 1px solid #995bce;
  padding: 3px 7px 2px 7px;
}

.customers th {
  font-size: 1.1em;
  text-align: left;
  padding-top: 5px;
  padding-bottom: 4px;
  background-color: #995bce;
  color: #ffffff;
}

.customers tr.alt td {
  color: #000000;
  background-color: #eaf2d3;
}
.fm {
  margin: 0 auto;
  border: 1px solid #995bce;
  border-radius: 5px;
  width: 90%;
  padding: 5px 10px 15px 10px;
}
.cur_nirt {
  width: 100%;
  max-width: 1100px;
  min-width: 900px;
  display: flex;
  justify-content: center;
}
